.site_main {
  height: 100%;
  margin-left: 200px;
  position: relative;
  background-color: #fff;
}

.pt-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 25px 5%;
  box-sizing: border-box;
  background: #f5f5f5 url(../assets/bg.png) center center / cover no-repeat scroll;
}

.subpages {
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  max-width: 1240px;
}

.middle_position {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: calc(100% - 120px);
}

.row {
  display: flex;

  .row_item {
    flex: 1;
    padding: 0 15px;
  }
}

.section_without_bg {
  background-color: transparent !important;
  box-shadow: none !important;
}

.section_title_block {
  position: relative;
  margin-bottom: 40px;
  text-align: center;

  .section_title_wrapper {
    display: inline-block;
    position: relative;
    text-align: right;

    .section_title {
      position: relative;
      display: block;
      width: 100%;
      margin: 12px 0 0;
      font-size: 60px;
      color: #eee;
      line-height: 1.1em;
      z-index: 1;
    }

    .section_description {
      display: inline-block;
      position: relative;
      color: #fff;
      background-color: #ff724c;
      padding: 0 10px;
      font-size: 14px;
      font-weight: 200;
      text-align: center;
      z-index: 2;
    }
  }

}

.section_inner {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 20px 30px 50px;
  overflow-y: auto;
  box-sizing: border-box;

  &::before {
    content: '';
    display: inline-block;
    position: absolute;
    background: transparent;
    width: 110px;
    height: 110px;
    border: 3px solid #ff724c;
    border-width: 3px 0 0 3px;
    top: 20px;
    left: 20px;
    z-index: 0;
  }

  &::after {
    content: '';
    display: inline-block;
    position: absolute;
    background: transparent;
    width: 110px;
    height: 110px;
    border: 3px solid #ff724c;
    border-width: 0 3px 3px 0;
    right: 20px;
    bottom: 20px;
    z-index: 0;
  }

  &::-webkit-scrollbar {
    width: 4px;
  }

  &::-webkit-scrollbar-thumb {
    background: #ff724c;
    border-radius: 10px;
  }

  &::-webkit-scrollbar-track {
    background: #ffdcd3
  }
}

.pt-page {
  position: absolute;
  display: table;
  background-color: #fff;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  padding: 10px 0;
  opacity: 0;
  visibility: hidden;
  transform: scale(1);
  transition: all 0.7s ease;
  z-index: 0;
  box-shadow: 0 1px 33px rgba(0, 0, 0, 0.15);
  overflow: hidden;

  &.pt-page-current {
    opacity: 1;
    visibility: visible;
    z-index: 1;
  }

  &.pt-page-exit {
    animation: zoomOut 0.7s forwards;
    z-index: 2;
  }

  &.zoom-in {
    animation: zoomIn 0.7s forwards;
  }

  &.slide-from-right {
    animation: slideFromRight 0.7s forwards;
  }

  &.slide-from-left {
    animation: slideFromLeft 0.7s forwards;
  }

  &.slide-from-bottom {
    animation: slideFromBottom 0.7s forwards;
  }

  &.slide-from-top {
    animation: slideFromTop 0.7s forwards;
  }
}

/* Exit animation: Zoom out */
@keyframes zoomOut {
  0% {
    opacity: 1;
    transform: scale(1);
  }

  100% {
    opacity: 0;
    transform: scale(0.7);
  }
}

/* Enter animations */
@keyframes zoomIn {
  0% {
    opacity: 0;
    transform: scale(1.3);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes slideFromRight {
  0% {
    opacity: 0;
    transform: translateX(100%);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideFromLeft {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideFromBottom {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideFromTop {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 768px) {
  .site_main {
    margin-left: 0;
  }

  .pt-wrapper {
    padding: 70px 20px;
  }

  .pt-page {
    padding: 20px 0;
  }
  .section_title_block .section_title_wrapper .section_title {
    font-size: 48px;
  }

  .contact_section .circle_4,
  .contact_section .circle_5 {
    display: none;
  }
}
@media (max-width: 425px) {
  .section_inner {
    padding: 20px 10px;

    &::before,
    &::after {
      display: none;
    }

    &::-webkit-scrollbar {
      width: 0;
    }
  }
  .section_title_block .section_title_wrapper {
    text-align: center;

    .section_title {
      font-size: 40px;
      margin: 0;
    }
  }
}